import React from 'react';
import styled from 'styled-components';

const TopBrandItem = ({ brand }) => {
  return (
    <TopBrandItemContainer>
      <TopBrandItemImageContainer>
        <TopBrandItemImage src={brand.cover} />
      </TopBrandItemImageContainer>
      <TopBrandItemTime>{brand.time}</TopBrandItemTime>
    </TopBrandItemContainer>
  );
};

export default TopBrandItem;
const TopBrandItemContainer = styled.div``;
const TopBrandItemImageContainer = styled.div`
  /* background: white; */
  box-shadow: rgb(0 0 0 / 8%) 0px 3px 12px;
  width: 9rem;
  height: 9rem;
  border-radius: 10%;
  border: 0.5px solid rgb(232, 232, 232);
`;
const TopBrandItemImage = styled.img`
  width: 100%;
  height: 100%;
  object-fit: contain;
  border-radius: inherit;
`;
const TopBrandItemTime = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.9rem;
  margin-top: 0.2rem;
`;
