import React from 'react';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import ArrowNext from './arrowNext';
import ArrowPrevious from './arrowPrevious';
import Collection from './collection';
import styled from 'styled-components';
const collectionList = [
  {
    id: 1,
    title: 'Newly Opened',
    subTitle: '15 places',
    cover: '/images/collections/newlyopened.jpg',
  },
  {
    id: 2,
    title: 'Trending This Week',
    subTitle: '30 places',
    cover: '/images/collections/trending.jpg',
  },
  {
    id: 3,
    title: 'Best of Laroo',
    subTitle: '45 places',
    cover: '/images/collections/best.jpg',
  },
  {
    id: 4,
    title: 'Love Pizza',
    subTitle: '12 places',
    cover: '/images/collections/pizza.jpg',
  },
  {
    id: 5,
    title: 'Brilliant Bryanis',
    subTitle: '15 places',
    cover: '/images/collections/brylliant.jpg',
  },
  {
    id: 6,
    title: 'Darnishi',
    subTitle: '12 places',
    cover: '/images/collections/danishi.jpg',
  },
  {
    id: 7,
    title: 'Flavours of Laroo',
    subTitle: '22 places',
    cover: '/images/collections/flavours.jpg',
  },
  {
    id: 8,
    title: 'Sweet Tooth',
    subTitle: '22 places',
    cover: '/images/collections/sweettooth.jpg',
  },
];

const settings = {
  infinite: false,
  slidesToShow: 4,
  slidesToScroll: 1,
  nextArrow: <ArrowNext />,
  prevArrow: <ArrowPrevious />,
};
const CollectionsComponent = () => {
  return (
    <CollectionContainer>
      <CollectionContainerTitle>Collections</CollectionContainerTitle>
      <ContainerTitleElements>
        <ContainerLeftText>
          Explore curated lists of top restaurants, cafes, pubs, and bars in
          Laroo, based on trends
        </ContainerLeftText>
        <ContainerRightText>All Collections</ContainerRightText>
      </ContainerTitleElements>
      <Slider {...settings}>
        {collectionList.map((item) => (
          <Collection key={item.id} item={item} />
        ))}
      </Slider>
    </CollectionContainer>
  );
};

export default CollectionsComponent;
const CollectionContainer = styled.div`
  padding: 1.5rem 0 4rem;
  background: rgb(248 248 248);
  .slick-prev:before {
    color: black;
    padding: 1rem;
    font-size: 1.1rem;
  }
  .slick-next:before {
    color: black;
    padding: 1rem;
    font-size: 1.1rem;
  }
`;
const CollectionContainerTitle = styled.div`
  font-size: 2rem;
`;
const ContainerTitleElements = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;
const ContainerLeftText = styled.div`
  color: #363636;
  font-size: 1.1rem;
  line-height: 1.4;
`;
const ContainerRightText = styled.div`
  cursor: pointer;
  color: #ef4f5f;
`;
