import React from 'react';
import styled from 'styled-components';
import RestaurantCard from './restaurantCard';

const ExploreSection = ({ restaurants, collectionName }) => {
  return (
    <ExploreSectionContainer>
      <ExploreSectionTitle>{collectionName}</ExploreSectionTitle>
      <ExploreSectionGrid>
        {restaurants.map((restaurant) => (
          <RestaurantCard restaurant={restaurant} key={restaurant.info.resId} />
        ))}
      </ExploreSectionGrid>
    </ExploreSectionContainer>
  );
};

export default ExploreSection;
const ExploreSectionContainer = styled.div`
  width: 100%;
  /* padding: 2rem 0; */
  margin-top: 1rem;
`;

const ExploreSectionTitle = styled.div`
  font-size: 1.8rem;
  font-weight: 600;
  margin-bottom: 1rem;
  color: #1c1c1c;
`;

const ExploreSectionGrid = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 2rem 0;
`;
