import React from 'react';
import styled from 'styled-components';
import { AiFillCaretRight } from 'react-icons/ai';
const Collection = ({ item }) => {
  return (
    <CollectionContainer>
      <CollectionImageContainer>
        <CollectionImage src={item.cover} />
        <WordsSection>
          <ConstCollectionTitle>{item.title}</ConstCollectionTitle>
          <CollectionPlaces>
            {item.subTitle} <AllIcon />
          </CollectionPlaces>
        </WordsSection>
      </CollectionImageContainer>
    </CollectionContainer>
  );
};

export default Collection;
const CollectionContainer = styled.div`
  margin-top: 1.3rem;
`;
const CollectionImageContainer = styled.div`
  position: relative;
  height: 20rem;
  width: 13rem;
  border-radius: 0.8rem;

  @media (min-width: 1040px) {
    width: 16rem;
  }
`;
const CollectionImage = styled.img`
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 0.8rem;
`;
const WordsSection = styled.div`
  backdrop-filter: blur(1px);
  position: absolute;
  bottom: 2rem;
  left: 0.9rem;
`;
const AllIcon = styled(AiFillCaretRight)`
  display: flex;
  justify-content: center;
  align-items: center;
`;
const ConstCollectionTitle = styled.div`
  display: flex;
  align-items: center;
  color: #fff;
  font-size: 1.2rem;
`;
const CollectionPlaces = styled.div`
  position: absolute;
  display: flex;
  align-items: center;
  color: #fff;
`;
