import React from 'react';
import ArrowNext from './arrowNext';
import ArrowPrevious from './arrowPrevious';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import styled from 'styled-components';
import TopBrandItem from './topBrandItem';

const TopBrands = () => {
  const topBrandsList = [
    {
      id: 1,
      time: '35 min',
      cover: '/images/restaurants/mc donald.jpg',
    },
    {
      id: 2,
      time: '29 min',
      cover: '/images/restaurants/burgerking.jpg',
    },
    {
      id: 3,
      time: '32 min',
      cover: '/images/restaurants/subway.jpg',
    },
    {
      id: 4,
      time: '30 min',
      cover: '/images/restaurants/haldiram.jpg',
    },
    {
      id: 6,
      time: '38 min',
      cover: '/images/restaurants/starbucks.jpg',
    },
    {
      id: 7,
      time: '31 min',
      cover: '/images/restaurants/carterpoint.jpg',
    },
    {
      id: 8,
      time: '30 min',
      cover: '/images/restaurants/chaayos.jpg',
    },
    {
      id: 9,
      time: '40 min',
      cover: '/images/restaurants/bikanavera.jpg',
    },
    {
      id: 10,
      time: '35 min',
      cover: '/images/restaurants/theobrema.jpg',
    },
  ];
  const settings = {
    infinite: false,
    slidesToShow: 6,
    slidesToScroll: 1,
    nextArrow: <ArrowNext />,
    prevArrow: <ArrowPrevious />,
  };
  return (
    <TopBrandsContainer>
      <TopBrandsTitle>Top Brands For You</TopBrandsTitle>
      <Slider {...settings}>
        {topBrandsList.map((brand) => (
          <TopBrandItem key={brand.id} brand={brand} />
        ))}
      </Slider>
    </TopBrandsContainer>
  );
};

export default TopBrands;
const TopBrandsContainer = styled.div`
  width: 100%;
  .slick-prev:before {
    color: black;
    padding: 1rem;
    font-size: 1.1rem;
  }
  .slick-next:before {
    color: black;
    padding: 1rem;
    font-size: 1.1rem;
  }
`;
const TopBrandsTitle = styled.div`
  font-size: 1.8rem;
  font-weight: 600;
  margin-bottom: 1rem;
  color: #1c1c1c;
`;
