import React from 'react';
import styled from 'styled-components';
import { AiFillStar } from 'react-icons/ai';

const RestaurantCard = ({ restaurant }) => {
  const name = restaurant?.info?.name ?? '';
  const image = restaurant?.info?.image?.url;
  const deliveryTime = restaurant?.order?.deliveryTime;
  const rating = restaurant?.info?.rating?.rating_text;
  const approxPrice = restaurant?.info?.cfo?.text;
  const offers = restaurant?.bulkOffers ?? [];
  const cuisines = restaurant?.info?.cuisine.map((c) => c.name).slice(0, 3);
  const bottomContainers = restaurant?.bottomContainers;
  const goldOffer = restaurant?.gold?.text;
  const proOffer = offers.length > 1 ? offers[0].text : null;
  const discountOffer =
    offers.length > 1
      ? offers[1].text
      : offers.length === 1
      ? offers[0].length
      : null;
  return (
    <RestaurantCardContainer>
      <RestaurantCover>
        <RestaurantImage src={image} />
        <DeliveryTime>{deliveryTime}</DeliveryTime>
        {proOffer && <ProOffer>{proOffer}</ProOffer>}
        {goldOffer && <GoldOffer>{goldOffer}</GoldOffer>}
        {discountOffer && <DiscountOffer>{discountOffer}</DiscountOffer>}
      </RestaurantCover>
      <RestaurantRow>
        <RestaurantName>{name}</RestaurantName>
        {rating && (
          <RestaurantRating>
            {rating} <RatingIcon />
          </RestaurantRating>
        )}
      </RestaurantRow>
      <RestaurantRow>
        {cuisines.length && (
          <RestaurantCuisines>{cuisines.join(',')}</RestaurantCuisines>
        )}
        <RestaurantPrice>{approxPrice}</RestaurantPrice>
      </RestaurantRow>
      <RestaurantCardSeparator></RestaurantCardSeparator>
      {bottomContainers.length ? (
        <RestaurantBottom>
          <RestaurantRowBottom>
            <RestaurantBottomLeft>
              <RestaurantBottomImage src={bottomContainers[0].image.url} />
              <RestaurantBottomText>
                {bottomContainers[0].text}
              </RestaurantBottomText>
            </RestaurantBottomLeft>
          </RestaurantRowBottom>
        </RestaurantBottom>
      ) : (
        ''
      )}
    </RestaurantCardContainer>
  );
};

export default RestaurantCard;
const RestaurantCardContainer = styled.div`
  padding: 0.7rem;
  margin: 0.8rem 0px 1.6rem 0px;
  border-radius: 1rem;
  border: 1px solid transparent;
  cursor: pointer;

  &:hover {
    background: #fff;
    border: 1px solid lightgrey;
  }
`;
const RestaurantCover = styled.div`
  height: 15rem;
  width: 16rem;
  border-radius: 1rem;
  position: relative;
  @media (min-width: 1024px) {
    width: 20rem;
  }
`;
const RestaurantImage = styled.img`
  object-fit: cover;
  width: 100%;
  height: 100%;
  border-radius: inherit;
`;
const DeliveryTime = styled.div`
  position: absolute;
  right: 0.9rem;
  bottom: 0.9rem;
  opacity: 0.8;
  color: #363636;
  background: #fff;
  backdrop-filter: blur(4px);
  border-radius: 4px;
  padding: 0.1rem 0.3rem;
  font-size: 0.8rem;
  font-weight: 600;
`;
const ProOffer = styled.div`
  background: #d92662;
  color: white;
  position: absolute;
  left: 0px;
  bottom: 2.5rem;
  overflow: hidden;
  text-overflow: ellipsis;
  border-radius: 0px 2px 2px 0px;
  padding: 0px 0.2rem;
  white-space: nowrap;
  font-size: 0.9rem;
  font-weight: 500;
  line-height: 18px;
`;
const GoldOffer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  background: #d92662;
  color: white;
  position: absolute;
  left: 0px;
  bottom: 0.9rem;
  overflow: hidden;
  text-overflow: ellipsis;
  border-radius: 0px 2px 2px 0px;
  padding: 0px 0.2rem;
  white-space: nowrap;
  font-size: 0.9rem;
  font-weight: 500;
  line-height: 18px;
`;
const DiscountOffer = styled.div`
  background: #256fef;
  color: white;
  position: absolute;
  left: 0px;
  bottom: 0.9rem;
  overflow: hidden;
  text-overflow: ellipsis;
  border-radius: 0px 2px 2px 0px;
  padding: 0px 0.2rem;
  white-space: nowrap;
  font-size: 0.9rem;
  font-weight: 500;
  line-height: 18px;
`;
const RestaurantRow = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.3rem;
`;
const RestaurantName = styled.div`
  max-width: 12.5rem;
  white-space: nowrap;
  font-weight: 600;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 1.1rem;
`;
const RestaurantRating = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  background: #267e3e;
  color: #fff;
  padding: 0px 0.5rem;
  font-weight: 600;
  border-radius: 0.3rem;
  font-size: 0.8rem;
  height: 1.2rem;
`;
const RatingIcon = styled(AiFillStar)`
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 0.7rem;
`;
const RestaurantCuisines = styled.span`
  width: 8rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 0.92rem;
  color: #363636;

  @media (min-width: 1024px) {
    width: 12rem;
  }
`;

const RestaurantPrice = styled.div`
  color: #363636;
  font-weight: 500;
`;
const RestaurantBottom = styled.div`
  color: #363636;
`;
const RestaurantCardSeparator = styled.div`
  height: 0.1rem;
  background: rgb(232, 232, 232, 232);
  margin: 0.5rem 0 1rem;
`;
const RestaurantRowBottom = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
`;
const RestaurantBottomLeft = styled.div`
  display: flex;
  align-items: center;
`;
const RestaurantBottomText = styled.div`
  font-size: 0.75rem;
  max-width: 14rem;
`;
const RestaurantBottomImage = styled.img`
  height: 1rem;
  margin-right: 0.2rem;
`;
