import React from 'react';
import { RiArrowUpDownFill } from 'react-icons/ri';
import { VscSettings } from 'react-icons/vsc';

import styled from 'styled-components';
import CollectionsComponent from './commons/collectionsComponent';
import ExploreSection from './commons/exploreSection';
import Filters from './commons/filters';
import { dining } from '../data/dining';

const DiningOut = () => {
  const dinningFilters = [
    {
      id: 1,
      title: 'Filters',
      icon: <IconSettings />,
    },
    {
      id: 2,
      title: 'Delivery Time',
      icon: <IconDelivery />,
    },
    {
      id: 3,
      title: 'Online Booking',
    },
    {
      id: 4,
      title: 'Rating: 4.0+',
    },
    {
      id: 5,
      title: 'Cuisines',
    },
    {
      id: 6,
      title: 'Outdoor Seating',
    },
    {
      id: 7,
      title: 'Pro Offers',
    },
    {
      id: 8,
      title: 'pure veg',
    },
  ];
  return (
    <DinningOutContainer>
      <CollectionsComponent />
      <Filters filters={dinningFilters} />
      <ExploreSection
        restaurants={dining}
        collectionName="Dine-Out Restaurants in Laroo"
      />
    </DinningOutContainer>
  );
};

export default DiningOut;
const DinningOutContainer = styled.div``;
const IconSettings = styled(VscSettings)`
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 0.3rem;
  font-size: 1rem;
`;

const IconDelivery = styled(RiArrowUpDownFill)`
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 0.3rem;
  font-size: 1rem;
`;
